import React, {useState, useEffect} from 'react';
import './AddPageMain.css';
import './react-popupbox.css';
import { useStateValue } from '../../StateProvider';
import { Link, useHistory } from 'react-router-dom';
import { PopupboxManager, PopupboxContainer } from 'react-popupbox';
import AddPagePointer from './AddPagePointer';
import TextField from '@material-ui/core/TextField';


function AddPageMain() {

    const [{jwt_token}, dispatch] = useStateValue();

    const history = useHistory();

    const [itemAttribute, setItemAttribute] = useState({skill_name: '', skill_path: '', skill_opinion: '', skill_rating: 0});

    const openPopupbox = () => {
        const content = (
            <div>
                <p className="quotes">Unauthorized Access</p><br/><hr/><br/>
                <p className="quotes">There is no proper JWT Token</p>
                <p className="quotes">You should Login before you add something</p>
                <span className="quotes-from">â€• Biglol</span>
            </div>
        )
        PopupboxManager.open({ content })
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        alert("ASDF");
    }

    const handleChange = (event) => {
        setItemAttribute({...itemAttribute, [event.target.name] : event.target.value});
    }

    useEffect(()=>{
        if(jwt_token){
            // alert("You do not have proper authentication");
            openPopupbox();
            setTimeout(function() {
                history.push('/');
            }, 3000);
        }
    },[])


    return (
        <div className="addPageMain">
            {
                <PopupboxContainer />
            }
            {
                !jwt_token && (
                    <main>
                        <section class="addPage">
                            <div class="details">
                                <h1>Elena Morell</h1>
                                <h2>Photographer</h2>
                                <p>02/08/2018</p>
                            </div>
                            <form onSubmit={handleSubmit}>
                                <div class="hero">
                                    <TextField
                                        required
                                        id="outlined-required"
                                        label="Skill name"
                                        name="skill_name"
                                        defaultValue=""
                                        variant="outlined"
                                        onChange={handleChange}
                                    />
                                    <TextField
                                        required
                                        id="outlined-required"
                                        label="Skill path"
                                        name="skill_path"
                                        defaultValue=""
                                        variant="outlined"
                                        onChange={handleChange}
                                    />
                                    <TextField
                                        required
                                        id="outlined-required"
                                        label="Skill opinion"
                                        name="skill_opinion"
                                        defaultValue=""
                                        variant="outlined"
                                        onChange={handleChange}
                                    />
                                    <TextField
                                        required
                                        id="outlined-required"
                                        label="Skill rating"
                                        name="skill_rating"
                                        defaultValue=""
                                        variant="outlined"
                                        onChange={handleChange}
                                    />
                                </div>
                            </form>
                        </section>
                        
                    </main>
                )
            }
            <AddPagePointer/>
        </div>
    )
}

export default AddPageMain
