export const initialState = {
    jwt_token: null
}

// Dispatching actions into the store and the reducer just listens and knows what to do
const reducer = ( state, action ) => {
    switch(action.type){
        case 'SET_USER':
            return{
                ...state,
                jwt_token: action.jwt_token
            }
        case 'SET_USER_NULL':
            return{
                ...state,
                jwt_token: null
            }
        default:
            return state;    
    }
}

export default reducer;